








import Vue from 'vue';
import {Component, Prop} from "vue-property-decorator";

@Component({
  name: 'toggable-side-menu-item'
})
export default class ToggableSideMenuItem extends Vue {
  @Prop({default: ''})
  title!: string;

  showHintContent = false;

  toggleMenuItem() {
    this.showHintContent = !this.showHintContent;
  }
}
